// Estilos específicos da análise livre

.separator {
  margin-top: 1rem;
}

.PageHeader {
  text-align: center;
  margin-bottom: 24px;
  width: 100%;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1rem;
}

.PageTitle {
  font-size: 24px;
  font-weight: 600;
  color: #333;
  margin: 0;
  padding: 0;
  margin-top: 1rem;
}

// Ajustar containers para ter mesma largura do tabuleiro
:global(.evaluation-container) {
  margin-top: 1rem;
  width: 100%;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}

.MovesSection {
  background-color: #f8f9fa;
  border-radius: 8px;
  padding: 20px;
  margin-top: 20px;
  width: 100%;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
}

.MovesTitle {
  font-size: 18px;
  font-weight: 600;
  color: #333;
  margin: 0 0 16px 0;
}

.MovesList {
  min-height: 60px;
}

.NoMoves {
  color: #666;
  font-style: italic;
  text-align: center;
  margin: 20px 0;
}

.MovesGrid {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  line-height: 1.6;
}

.Move {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  font-family: monospace;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;

  &:hover {
    background-color: #f0f0f0;
    border-color: #d0d0d0;
  }
}

.ActiveMove {
  font-weight: 700;
}

.MoveNumber {
  font-weight: 600;
  color: #666;
}

.MoveNotation {
  color: #333;
}

.ModalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 16px;
}

.Modal {
  background-color: white;
  border-radius: 8px;
  padding: 24px;
  width: 100%;
  max-width: 400px;
  max-height: 90vh;
  overflow-y: auto;
}

.ModalTitle {
  font-size: 1.5rem;
  font-weight: 600;
  color: #333;
  margin: 0 0 20px 0;
  text-align: center;
}

.FormGroup {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 16px;
}

.FormLabel {
  display: block;
  font-size: 14px;
  font-weight: 500;
  color: #333;
  margin-bottom: 6px;
}

.FormInput {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
  transition: border-color 0.2s ease;
  box-sizing: border-box;

  &:focus {
    outline: none;
    border-color: #3498db;
  }
}

.ModalActions {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  margin-top: 24px;
}

.ModalButton {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  min-width: 80px;

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

.CancelButton {
  background-color: #95a5a6;
  color: white;

  &:hover:not(:disabled) {
    background-color: #7f8c8d;
  }
}

.ConfirmButton {
  background-color: #3498db;
  color: white;

  &:hover:not(:disabled) {
    background-color: #2980b9;
  }
}

@media (max-width: 768px) {
  .PageHeader {
    margin-bottom: 20px;
    margin-top: 1rem;
  }

  .PageTitle {
    font-size: 20px;
  }

  .MovesSection {
    padding: 16px;
    border: 1px solid #ccc;
  }

  .MovesTitle {
    font-size: 16px;
  }

  .Move {
    font-size: 13px;
    padding: 3px 6px;
  }

  .Modal {
    padding: 20px;
  }

  .ModalActions {
    gap: 8px;
  }

  .ModalButton {
    flex: 1;
  }
}