.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 2rem;
}

.Modal {
  background: white;
  border-radius: 16px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  max-width: 500px;
  width: 100%;
  max-height: 80vh;
  overflow: hidden;
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  border-bottom: 1px solid #e0e0e0;
  background: #f8f9fa;
}

.Title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #333;
  margin: 0;
}

.CloseButton {
  background: none;
  border: none;
  font-size: 2rem;
  color: #666;
  cursor: pointer;
  padding: 0;
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: all 0.2s ease;

  &:hover {
    background: #e0e0e0;
    color: #333;
  }
}

.Content {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.FilterSection {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.FilterLabel {
  font-size: 1.1rem;
  font-weight: 600;
  color: #333;
}

.FilterSelect {
  padding: 0.75rem;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  font-size: 1rem;
  background: white;
  cursor: pointer;
  transition: border-color 0.3s ease;

  &:focus {
    outline: none;
    border-color: #4a90e2;
  }
}

.LevelButtons {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.LevelButton {
  padding: 0.5rem 1rem;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  background: white;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.9rem;
  min-width: 60px;
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    border-color: #4a90e2;
  }

  &.LevelButtonSelected {
    border-color: #4a90e2;
    background: #f0f8ff;
    color: #4a90e2;
    font-weight: 600;
  }
}

.Star {
  font-size: 1rem;
  transition: color 0.2s ease;

  &.StarFilled {
    color: #ffd700;
  }

  &.StarEmpty {
    color: #ddd;
  }
}

.ResultsSection {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 1rem;
  border-top: 1px solid #e0e0e0;
}

.PuzzleCount {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.CountNumber {
  font-size: 2rem;
  font-weight: 700;
  color: #4a90e2;
  line-height: 1;
}

.CountLabel {
  font-size: 0.9rem;
  color: #666;
  margin-top: 0.25rem;
}

.ApplyButton {
  background: #4a90e2;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 1rem 2rem;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(74, 144, 226, 0.3);

  &:hover:not(:disabled) {
    background: #357abd;
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(74, 144, 226, 0.4);
  }

  &:disabled {
    background: #ccc;
    cursor: not-allowed;
    box-shadow: none;
  }
}

@media (max-width: 768px) {
  .Overlay {
    padding: 1rem;
  }

  .Header {
    padding: 1rem 1.5rem;
  }

  .Title {
    font-size: 1.25rem;
  }

  .Content {
    padding: 1.5rem;
  }

  .LevelButtons {
    justify-content: center;
  }

  .ResultsSection {
    flex-direction: column;
    gap: 1rem;
    align-items: center;
  }

  .PuzzleCount {
    align-items: center;
  }

  .ApplyButton {
    width: 100%;
  }
}