.CreatePuzzleContainer {
  max-width: 400px;
  margin: 0 auto;
  width: 100%;
}

.CreatePuzzleHeader {
  text-align: center;
  margin-bottom: 30px;
}

.CreatePuzzleTitle {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 8px;
  font-weight: 600;
}

.CreatePuzzleSubtitle {
  color: #666;
  font-size: 16px;
}

.PuzzleForm {
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.FormGroup {
  margin-bottom: 25px;
}

.FormLabel {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  color: #333;
  font-size: 16px;
}

.FormInput {
  width: 100%;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 16px;
  box-sizing: border-box;
  transition: border-color 0.2s, box-shadow 0.2s;
}

.FormTextarea {
  width: 100%;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 14px;
  font-family: monospace;
  box-sizing: border-box;
  transition: border-color 0.2s, box-shadow 0.2s;
  resize: vertical;
  min-height: 250px;
  line-height: 1.4;
}

.FormInput:focus,
.FormTextarea:focus {
  outline: none;
  border-color: #2f74ab;
  box-shadow: 0 0 0 2px rgba(47, 116, 171, 0.2);
}

.FormActions {
  display: flex;
  gap: 15px;
  justify-content: center;
  margin-top: 30px;
  padding-top: 20px;
  border-top: 1px solid #eee;
}

.BtnPrimary {
  padding: 15px 30px;
  background-color: #2f74ab;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s;
}

.BtnPrimary:hover:not(:disabled) {
  background-color: #255d89;
}

.BtnPrimary:disabled {
  background-color: #bdc3c7;
  cursor: not-allowed;
}

.BtnSecondary {
  padding: 15px 30px;
  background-color: #6c757d;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s;
}

.BtnSecondary:hover {
  background-color: #5a6268;
}

.SuccessMessage,
.ErrorMessage {
  padding: 15px;
  border-radius: 6px;
  margin-bottom: 20px;
  text-align: center;
  font-weight: 500;
}

.SuccessMessage {
  background-color: #d4edda;
  color: #155724;
  border: 1px solid #c3e6cb;
}

.ErrorMessage {
  background-color: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
}